import React from 'react';
import { useCart } from '../../contexts/CartContext';
import { useAuth } from '../../contexts/AuthContext';
import { Button } from '../ui/button';
import { Badge } from '../ui/badge';
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from '../ui/sheet';
import { ShoppingCart, Plus, Minus, Trash2, Loader2 } from 'lucide-react';
import { Link } from 'react-router-dom';

const CartModal = ({ children }) => {
  const { cartItems, cartCount, cartTotal, loading, updateQuantity, removeFromCart } = useCart();
  const { isAuthenticated, user } = useAuth();

  const handleUpdateQuantity = (productId, newQuantity) => {
    if (newQuantity < 1) {
      removeFromCart(productId);
    } else {
      updateQuantity(productId, newQuantity);
    }
  };

  const getDiscountedPrice = (price) => {
    if (user && user.discount && user.discount > 0) {
      const discountAmount = (price * user.discount) / 100;
      return price - discountAmount;
    }
    return price;
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(price);
  };

  if (!isAuthenticated) {
    return (
      <Sheet>
        <SheetTrigger asChild>
          {children}
        </SheetTrigger>
        <SheetContent>
          <SheetHeader>
            <SheetTitle className="flex items-center space-x-2">
              <ShoppingCart className="h-5 w-5" />
              <span>Shopping Cart</span>
            </SheetTitle>
          </SheetHeader>
          <div className="flex flex-col items-center justify-center h-full space-y-4">
            <ShoppingCart className="h-16 w-16 text-gray-300" />
            <p className="text-gray-600">Please log in to view your cart</p>
            <Button className="bg-red-600 hover:bg-red-700">
              Login
            </Button>
          </div>
        </SheetContent>
      </Sheet>
    );
  }

  return (
    <Sheet>
      <SheetTrigger asChild>
        {children}
      </SheetTrigger>
      <SheetContent className="w-full sm:max-w-md max-h-[85vh]">
        <SheetHeader>
          <SheetTitle className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              <ShoppingCart className="h-5 w-5" />
              <span>Shopping Cart</span>
            </div>
            {cartCount > 0 && (
              <Badge variant="destructive" className="bg-red-600">
                {cartCount}
              </Badge>
            )}
          </SheetTitle>
          <SheetDescription>
            {cartCount === 0 ? 'Your cart is empty' : `${cartCount} item${cartCount > 1 ? 's' : ''} in your cart`}
          </SheetDescription>
        </SheetHeader>

        <div className="flex flex-col h-full">
          {loading ? (
            <div className="flex items-center justify-center py-8">
              <Loader2 className="h-6 w-6 animate-spin" />
            </div>
          ) : cartItems.length === 0 ? (
            <div className="flex flex-col items-center justify-center flex-1 space-y-4">
              <ShoppingCart className="h-16 w-16 text-gray-300" />
              <p className="text-gray-600">Your cart is empty</p>
              <Link to="/products">
                <Button className="bg-red-600 hover:bg-red-700">
                  Browse Products
                </Button>
              </Link>
            </div>
          ) : (
            <>
              {/* Cart Items */}
              <div className="flex-1 overflow-y-auto space-y-4 py-4 max-h-[60vh]">
                {cartItems.map((item) => {
                  // Check if item and item.product exist
                  if (!item || !item.product) {
                    console.warn('Invalid cart item:', item);
                    return null;
                  }
                  
                  return (
                    <div key={item.id} className="flex space-x-4 p-4 border rounded-lg">
                      <div className="flex-shrink-0">
                        <img
                          src={item.product.images?.[0] || '/placeholder-product.jpg'}
                          alt={item.product.name}
                          className="w-16 h-16 object-cover rounded-md"
                          onError={(e) => {
                            e.target.src = '/placeholder-product.jpg';
                          }}
                        />
                      </div>
                      
                      <div className="flex-1 min-w-0">
                        <h3 className="text-sm font-medium text-gray-900 truncate">
                          {item.product.name}
                        </h3>
                        <div className="text-sm text-gray-500">
                          {user && user.discount && user.discount > 0 ? (
                            <div className="space-y-1">
                              <span className="text-red-600 font-medium">
                                {formatPrice(getDiscountedPrice(item.product.price))}
                              </span>
                              <span className="text-gray-400 line-through">
                                {formatPrice(item.product.price)}
                              </span>
                              <Badge variant="destructive" className="text-xs">
                                -{user.discount}%
                              </Badge>
                            </div>
                          ) : (
                            <span>{formatPrice(item.product.price)}</span>
                          )}
                        </div>
                        
                        {!item.product.in_stock && (
                          <p className="text-xs text-red-600">Out of Stock</p>
                        )}
                      </div>
                      
                      <div className="flex flex-col space-y-2">
                        {/* Quantity Controls */}
                        <div className="flex items-center space-x-1">
                          <Button
                            size="sm"
                            variant="outline"
                            className="h-6 w-6 p-0"
                            onClick={() => handleUpdateQuantity(item.product.id, item.quantity - 1)}
                            disabled={loading}
                          >
                            <Minus className="h-3 w-3" />
                          </Button>
                          
                          <span className="text-sm w-8 text-center">{item.quantity}</span>
                          
                          <Button
                            size="sm"
                            variant="outline"
                            className="h-6 w-6 p-0"
                            onClick={() => handleUpdateQuantity(item.product.id, item.quantity + 1)}
                            disabled={loading || item.quantity >= (item.product.stock_quantity || 0)}
                          >
                            <Plus className="h-3 w-3" />
                          </Button>
                        </div>
                        
                        {/* Remove Button */}
                        <Button
                          size="sm"
                          variant="ghost"
                          className="h-6 text-red-600 hover:text-red-700 hover:bg-red-50"
                          onClick={() => removeFromCart(item.product.id)}
                          disabled={loading}
                        >
                          <Trash2 className="h-3 w-3" />
                        </Button>
                        
                        {/* Item Total */}
                        <p className="text-sm font-medium text-right">
                          ${item.item_total.toFixed(2)}
                        </p>
                      </div>
                    </div>
                  );
                })}
              </div>

              {/* Cart Summary */}
              <div className="border-t pt-4 space-y-4">
                {user && user.discount && user.discount > 0 && (
                  <div className="flex justify-between items-center text-sm text-gray-600">
                    <span>Discount Applied:</span>
                    <Badge variant="destructive" className="text-xs">
                      -{user.discount}%
                    </Badge>
                  </div>
                )}
                <div className="flex justify-between items-center">
                  <span className="text-lg font-semibold">Total:</span>
                  <span className="text-lg font-bold text-red-600">
                    {formatPrice(cartTotal)}
                  </span>
                </div>
                
                <div className="space-y-2">
                  <Link to="/cart" className="block">
                    <Button variant="outline" className="w-full">
                      View Cart
                    </Button>
                  </Link>
                  
                  <Link to="/checkout" className="block">
                    <Button className="w-full bg-red-600 hover:bg-red-700">
                      Checkout
                    </Button>
                  </Link>
                </div>
              </div>
            </>
          )}
        </div>
      </SheetContent>
    </Sheet>
  );
};

export default CartModal;