import React from 'react';
import { useCart } from '../contexts/CartContext';
import { useAuth } from '../contexts/AuthContext';
import Header from '../components/Header';
import Footer from '../components/Footer';
import { Button } from '../components/ui/button';
import { Card, CardContent, CardHeader, CardTitle } from '../components/ui/card';
import { Plus, Minus, Trash2, ShoppingCart, ArrowLeft } from 'lucide-react';
import { Link, useNavigate } from 'react-router-dom';
import LoginModal from '../components/auth/LoginModal';

const CartPage = () => {
  const { cartItems, cartCount, cartTotal, loading, updateQuantity, removeFromCart, clearCart } = useCart();
  const { isAuthenticated } = useAuth();
  const navigate = useNavigate();

  const handleUpdateQuantity = (productId, newQuantity) => {
    if (newQuantity < 1) {
      removeFromCart(productId);
    } else {
      updateQuantity(productId, newQuantity);
    }
  };

  const handleClearCart = async () => {
    if (window.confirm('Are you sure you want to clear your cart?')) {
      await clearCart();
    }
  };

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen bg-white">
        <Header />
        
        <div className="container mx-auto px-4 py-16">
          <div className="max-w-2xl mx-auto text-center">
            <ShoppingCart className="h-16 w-16 text-gray-300 mx-auto mb-4" />
            <h1 className="text-3xl font-bold text-gray-800 mb-4">Shopping Cart</h1>
            <p className="text-gray-600 mb-8">Please log in to view your cart and make purchases.</p>
            
            <div className="space-y-4">
              <LoginModal>
                <Button size="lg" className="bg-red-600 hover:bg-red-700">
                  Login to View Cart
                </Button>
              </LoginModal>
              
              <div>
                <Link to="/products">
                  <Button variant="outline" size="lg">
                    Continue Shopping
                  </Button>
                </Link>
              </div>
            </div>
          </div>
        </div>
        
        <Footer />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-white">
      <Header />
      
      <div className="container mx-auto px-4 py-8">
        {/* Header */}
        <div className="flex items-center justify-between mb-8">
          <div className="flex items-center space-x-4">
            <Button
              variant="ghost"
              onClick={() => navigate(-1)}
              className="text-red-600 hover:text-red-700"
            >
              <ArrowLeft className="h-4 w-4 mr-2" />
              Back
            </Button>
            <h1 className="text-3xl font-bold text-gray-800">Shopping Cart</h1>
          </div>
          
          {cartCount > 0 && (
            <Button
              variant="outline"
              onClick={handleClearCart}
              className="text-red-600 border-red-600 hover:bg-red-50"
            >
              <Trash2 className="h-4 w-4 mr-2" />
              Clear Cart
            </Button>
          )}
        </div>

        {cartItems.length === 0 ? (
          <div className="text-center py-16">
            <ShoppingCart className="h-16 w-16 text-gray-300 mx-auto mb-4" />
            <h2 className="text-2xl font-semibold text-gray-800 mb-4">Your cart is empty</h2>
            <p className="text-gray-600 mb-8">Looks like you haven't added any items to your cart yet.</p>
            
            <Link to="/products">
              <Button size="lg" className="bg-red-600 hover:bg-red-700">
                Browse Products
              </Button>
            </Link>
          </div>
        ) : (
          <div className="grid lg:grid-cols-3 gap-8">
            {/* Cart Items */}
            <div className="lg:col-span-2 space-y-4">
              {cartItems.map((item) => (
                <Card key={item.id}>
                  <CardContent className="p-6">
                    <div className="flex space-x-4">
                      <div className="flex-shrink-0">
                        <img
                          src={item.product.images?.[0] || '/placeholder-product.jpg'}
                          alt={item.product.name}
                          className="w-24 h-24 object-cover rounded-lg"
                        />
                      </div>
                      
                      <div className="flex-1 min-w-0">
                        <Link to={`/products/${item.product.id}`}>
                          <h3 className="text-lg font-semibold text-gray-900 hover:text-red-600 transition-colors">
                            {item.product.name}
                          </h3>
                        </Link>
                        
                        <p className="text-sm text-gray-500 mt-1">
                          ${item.product.price.toFixed(2)} each
                        </p>
                        
                        {!item.product.in_stock && (
                          <p className="text-sm text-red-600 mt-1">Out of Stock</p>
                        )}
                        
                        {item.product.stock_quantity < 10 && item.product.in_stock && (
                          <p className="text-sm text-orange-600 mt-1">
                            Only {item.product.stock_quantity} left in stock
                          </p>
                        )}
                      </div>
                      
                      <div className="flex flex-col items-end space-y-4">
                        {/* Quantity Controls */}
                        <div className="flex items-center space-x-2">
                          <Button
                            size="sm"
                            variant="outline"
                            className="h-8 w-8 p-0"
                            onClick={() => handleUpdateQuantity(item.product.id, item.quantity - 1)}
                            disabled={loading}
                          >
                            <Minus className="h-4 w-4" />
                          </Button>
                          
                          <span className="text-sm font-medium w-12 text-center">
                            {item.quantity}
                          </span>
                          
                          <Button
                            size="sm"
                            variant="outline"
                            className="h-8 w-8 p-0"
                            onClick={() => handleUpdateQuantity(item.product.id, item.quantity + 1)}
                            disabled={loading || item.quantity >= (item.product.stock_quantity || 0)}
                          >
                            <Plus className="h-4 w-4" />
                          </Button>
                        </div>
                        
                        {/* Item Total */}
                        <p className="text-lg font-semibold text-gray-900">
                          ${item.item_total.toFixed(2)}
                        </p>
                        
                        {/* Remove Button */}
                        <Button
                          size="sm"
                          variant="ghost"
                          className="text-red-600 hover:text-red-700 hover:bg-red-50"
                          onClick={() => removeFromCart(item.product.id)}
                          disabled={loading}
                        >
                          <Trash2 className="h-4 w-4 mr-1" />
                          Remove
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
            
            {/* Order Summary */}
            <div className="lg:col-span-1">
              <Card className="sticky top-4">
                <CardHeader>
                  <CardTitle>Order Summary</CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                  <div className="flex justify-between">
                    <span>Items ({cartCount}):</span>
                    <span>${cartTotal.toFixed(2)}</span>
                  </div>
                  
                  <div className="flex justify-between">
                    <span>Shipping:</span>
                    <span className="text-green-600">
                      {cartTotal >= 500 ? 'FREE' : '$25.00'}
                    </span>
                  </div>
                  
                  <div className="flex justify-between">
                    <span>Tax:</span>
                    <span>${(cartTotal * 0.08).toFixed(2)}</span>
                  </div>
                  
                  <div className="border-t pt-4">
                    <div className="flex justify-between text-lg font-semibold">
                      <span>Total:</span>
                      <span className="text-red-600">
                        ${(cartTotal + (cartTotal >= 500 ? 0 : 25) + (cartTotal * 0.08)).toFixed(2)}
                      </span>
                    </div>
                  </div>
                  
                  {cartTotal < 500 && (
                    <p className="text-sm text-gray-600">
                      Add ${(500 - cartTotal).toFixed(2)} more for free shipping!
                    </p>
                  )}
                  
                  <div className="space-y-3 pt-4">
                    <Link to="/checkout">
                      <Button className="w-full bg-red-600 hover:bg-red-700" size="lg">
                        Proceed to Checkout
                      </Button>
                    </Link>
                    
                    <Link to="/products">
                      <Button variant="outline" className="w-full">
                        Continue Shopping
                      </Button>
                    </Link>
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        )}
      </div>
      
      <Footer />
    </div>
  );
};

export default CartPage;