import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';
import { useAuth } from './AuthContext';
import { useToast } from '../hooks/use-toast';
import { mockData } from '../data/mockData';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const CartContext = createContext();

export const useCart = () => {
  const context = useContext(CartContext);
  if (!context) {
    throw new Error('useCart must be used within a CartProvider');
  }
  return context;
};

export const CartProvider = ({ children }) => {
  const [cartItems, setCartItems] = useState([]);
  const [cartCount, setCartCount] = useState(0);
  const [cartTotal, setCartTotal] = useState(0);
  const [loading, setLoading] = useState(false);
  const { isAuthenticated, user } = useAuth();
  const { toast } = useToast();

  // Load cart when user is authenticated
  useEffect(() => {
    if (isAuthenticated) {
      loadCart();
    } else {
      // Clear cart when user logs out
      setCartItems([]);
      setCartCount(0);
      setCartTotal(0);
    }
  }, [isAuthenticated]);

  // Calculate discounted price for user
  const getDiscountedPrice = (price) => {
    if (user && user.discount && user.discount > 0) {
      const discountAmount = (price * user.discount) / 100;
      return price - discountAmount;
    }
    return price;
  };

  // Calculate cart total with discounts
  const calculateCartTotal = (items) => {
    let total = 0;
    items.forEach(item => {
      // Yeni structure-da price item.product.price-dədir
      const price = item.product ? item.product.price : item.price;
      const discountedPrice = getDiscountedPrice(price);
      total += discountedPrice * item.quantity;
    });
    return total;
  };

  const loadCart = async () => {
    try {
      setLoading(true);
      
      // Mock data ile çalış - backend olmadan
      const mockCartData = {
        items: [],
        total_items: 0,
        total_amount: 0
      };
      
      // LocalStorage'dan cart verilerini al
      const savedCart = localStorage.getItem('mock-cart');
      if (savedCart) {
        const parsedCart = JSON.parse(savedCart);
        
        // Köhnə data structure-nı yeni structure-a çevir
        const normalizedItems = (parsedCart.items || []).map(item => {
          // Əgər item.product varsa, yeni structure deməkdir
          if (item.product) {
            return {
              ...item,
              item_total: item.product.price * item.quantity
            };
          }
          
          // Köhnə structure: {product_id, name, price, quantity}
          // Yeni structure-a çevir: {id, product: {...}, quantity, item_total}
          return {
            id: item.product_id || item.id,
            product: {
              id: item.product_id || item.id,
              name: item.name,
              price: item.price,
              images: [], // Köhnə data-da image yoxdur
              in_stock: true, // Default olaraq true
              stock_quantity: 0 // Default olaraq 0
            },
            quantity: item.quantity,
            item_total: item.price * item.quantity
          };
        });
        
        setCartItems(normalizedItems);
        setCartCount(normalizedItems.reduce((sum, item) => sum + item.quantity, 0));
        // İndirimli toplam hesapla
        const discountedTotal = calculateCartTotal(normalizedItems);
        setCartTotal(discountedTotal);
        
        // Normalized data-nı localStorage-a kaydet
        localStorage.setItem('mock-cart', JSON.stringify({
          items: normalizedItems,
          total_items: normalizedItems.reduce((sum, item) => sum + item.quantity, 0),
          total_amount: normalizedItems.reduce((sum, item) => sum + (item.product.price * item.quantity), 0)
        }));
      } else {
        setCartItems(mockCartData.items);
        setCartCount(mockCartData.total_items);
        setCartTotal(mockCartData.total_amount);
      }
    } catch (error) {
      console.error('Failed to load cart:', error);
      // Hata durumunda boş cart göster
      setCartItems([]);
      setCartCount(0);
      setCartTotal(0);
    } finally {
      setLoading(false);
    }
  };

  const addToCart = async (productId, quantity = 1) => {
    if (!isAuthenticated) {
      toast({
        title: 'Login Required',
        description: 'Please log in to add items to your cart.',
        variant: 'destructive'
      });
      return { success: false };
    }

    try {
      setLoading(true);
      
      // Mock data ile çalış - backend olmadan
      // mockData.products-dan məhsul axtar
      const product = mockData.products.find(p => p.id === productId);
      if (!product) {
        throw new Error('Product not found');
      }
      
      // Mevcut cart'ı al
      const currentCart = JSON.parse(localStorage.getItem('mock-cart') || '{"items": [], "total_items": 0, "total_amount": 0}');
      
      // Ürün zaten cart'ta var mı kontrol et
      const existingItem = currentCart.items.find(item => item.id === productId);
      
      if (existingItem) {
        existingItem.quantity += quantity;
        existingItem.item_total = existingItem.product.price * existingItem.quantity;
      } else {
        currentCart.items.push({
          id: productId,
          product: {
            id: productId,
            name: product.name,
            price: product.price,
            images: product.image ? [product.image] : [],
            in_stock: product.in_stock !== undefined ? product.in_stock : true,
            stock_quantity: product.stock_quantity || 0
          },
          quantity: quantity,
          item_total: product.price * quantity
        });
      }
      
      // Toplamları güncelle
      currentCart.total_items = currentCart.items.reduce((sum, item) => sum + item.quantity, 0);
      currentCart.total_amount = currentCart.items.reduce((sum, item) => sum + (item.product.price * item.quantity), 0);
      
      // LocalStorage'a kaydet
      localStorage.setItem('mock-cart', JSON.stringify(currentCart));
      
      // State'i güncelle
      setCartItems(currentCart.items);
      setCartCount(currentCart.total_items);
      setCartTotal(currentCart.total_amount);
      
      toast({
        title: 'Added to Cart',
        description: 'Item has been added to your cart.',
      });

      return { success: true };
    } catch (error) {
      const message = error.message || 'Failed to add item to cart';
      toast({
        title: 'Error',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    } finally {
      setLoading(false);
    }
  };

  const updateQuantity = async (productId, quantity) => {
    try {
      setLoading(true);
      
      // Mock data ile çalış - backend olmadan
      const currentCart = JSON.parse(localStorage.getItem('mock-cart') || '{"items": [], "total_items": 0, "total_amount": 0}');
      
      // Ürün miktarını güncelle
      const item = currentCart.items.find(item => item.id === productId);
      if (item) {
        item.quantity = quantity;
        item.item_total = item.product.price * quantity;
        
        // Toplamları güncelle
        currentCart.total_items = currentCart.items.reduce((sum, item) => sum + item.quantity, 0);
        // İndirimli toplam hesapla
        const discountedTotal = calculateCartTotal(currentCart.items);
        
        // LocalStorage'a kaydet
        localStorage.setItem('mock-cart', JSON.stringify(currentCart));
        
        // State'i güncelle
        setCartItems(currentCart.items);
        setCartCount(currentCart.total_items);
        setCartTotal(discountedTotal);
      }
      
      toast({
        title: 'Cart Updated',
        description: 'Item quantity has been updated.',
      });

      return { success: true };
    } catch (error) {
      const message = error.message || 'Failed to update cart';
      toast({
        title: 'Error',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    } finally {
      setLoading(false);
    }
  };

  const removeFromCart = async (productId) => {
    try {
      setLoading(true);
      
      // Mock data ile çalış - backend olmadan
      const currentCart = JSON.parse(localStorage.getItem('mock-cart') || '{"items": [], "total_items": 0, "total_amount": 0}');
      
      // Ürünü cart'tan çıkar
      currentCart.items = currentCart.items.filter(item => item.id !== productId);
      
      // Toplamları güncelle
      currentCart.total_items = currentCart.items.reduce((sum, item) => sum + item.quantity, 0);
      // İndirimli toplam hesapla
      const discountedTotal = calculateCartTotal(currentCart.items);
      
      // LocalStorage'a kaydet
      localStorage.setItem('mock-cart', JSON.stringify(currentCart));
      
      // State'i güncelle
      setCartItems(currentCart.items);
      setCartCount(currentCart.total_items);
      setCartTotal(discountedTotal);
      
      toast({
        title: 'Removed from Cart',
        description: 'Item has been removed from your cart.',
      });

      return { success: true };
    } catch (error) {
      const message = error.message || 'Failed to remove item';
      toast({
        title: 'Error',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    } finally {
      setLoading(false);
    }
  };

  const clearCart = async () => {
    try {
      setLoading(true);
      
      // Mock data ile çalış - backend olmadan
      localStorage.removeItem('mock-cart');
      
      setCartItems([]);
      setCartCount(0);
      setCartTotal(0);
      
      toast({
        title: 'Cart Cleared',
        description: 'All items have been removed from your cart.',
      });

      return { success: true };
    } catch (error) {
      const message = error.message || 'Failed to clear cart';
      toast({
        title: 'Error',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    } finally {
      setLoading(false);
    }
  };

  const getCartCount = async () => {
    if (!isAuthenticated) return 0;
    
    try {
      // Mock data ile çalış - backend olmadan
      const savedCart = localStorage.getItem('mock-cart');
      if (savedCart) {
        const cartData = JSON.parse(savedCart);
        return cartData.total_items || 0;
      }
      return 0;
    } catch (error) {
      console.error('Failed to get cart count:', error);
      return 0;
    }
  };

  const value = {
    cartItems,
    cartCount,
    cartTotal,
    loading,
    addToCart,
    updateQuantity,
    removeFromCart,
    clearCart,
    loadCart,
    getCartCount,
  };

  return (
    <CartContext.Provider value={value}>
      {children}
    </CartContext.Provider>
  );
};