import React, { createContext, useContext, useState, useEffect } from 'react';
import axios from 'axios';
import { useToast } from '../hooks/use-toast';

const BACKEND_URL = process.env.REACT_APP_BACKEND_URL;
const API = `${BACKEND_URL}/api`;

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [token, setToken] = useState(localStorage.getItem('token'));
  const { toast } = useToast();

  // Set up axios interceptor for authentication
  useEffect(() => {
    if (token && !token.startsWith('mock-token-')) {
      axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
    } else {
      delete axios.defaults.headers.common['Authorization'];
    }
  }, [token]);

  // Check if user is authenticated on app load
  useEffect(() => {
    const checkAuth = async () => {
      if (token) {
        try {
          // Mock token kontrolü - backend olmadan çalışması için
          if (token.startsWith('mock-token-')) {
            // Mock token varsa, localStorage'dan user bilgisini al
            const mockUser = {
              id: "1",
              first_name: "Admin",
              last_name: "User",
              email: "admin@bilal-parts.com",
              role: "admin",
              is_active: true
            };
            setUser(mockUser);
          } else {
            // Gerçek backend API çağrısı
            const response = await axios.get(`${API}/auth/me`);
            setUser(response.data);
          }
        } catch (error) {
          console.error('Auth check failed:', error);
          // Backend hatası durumunda logout yapma, sadece mock data ile devam et
          if (!token.startsWith('mock-token-')) {
            logout();
          }
        }
      }
      setLoading(false);
    };

    checkAuth();
  }, [token]);

  const login = async (email, password) => {
    try {
      setLoading(true);
      
      // Mock login - test kullanıcıları
      const mockUsers = [
        {
          id: "1",
          first_name: "Admin",
          last_name: "User",
          email: "admin@bilal-parts.com",
          role: "admin",
          is_active: true
        },
        {
          id: "2",
          first_name: "John",
          last_name: "Doe",
          email: "john@example.com",
          role: "user",
          is_active: true
        }
      ];
      
      const mockUser = mockUsers.find(u => u.email === email);
      
      if (mockUser && password === "admin123") {
        setUser(mockUser);
        setToken("mock-token-" + Date.now());
        localStorage.setItem('token', "mock-token-" + Date.now());
        
        toast({
          title: 'Login Successful',
          description: `Welcome back, ${mockUser.first_name}!`,
        });

        return { success: true };
      } else {
        throw new Error("Invalid credentials");
      }
    } catch (error) {
      const message = error.message || 'Login failed';
      toast({
        title: 'Login Failed',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    } finally {
      setLoading(false);
    }
  };

  const register = async (userData) => {
    try {
      setLoading(true);
      
      // Mock registration
      const newUser = {
        id: "user-" + Date.now(),
        first_name: userData.first_name,
        last_name: userData.last_name,
        email: userData.email,
        phone: userData.phone,
        company_name: userData.company_name || '',
        country: userData.country || '',
        city: userData.city || '',
        vat_number: userData.vat_number || '',
        role: "customer",
        status: "pending",
        is_active: false,
        discount: 0,
        created_at: new Date().toISOString()
      };
      
      toast({
        title: 'Registration Successful',
        description: 'Your account has been created. Please log in.',
      });

      return { success: true, user: newUser };
    } catch (error) {
      const message = error.message || 'Registration failed';
      toast({
        title: 'Registration Failed',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    } finally {
      setLoading(false);
    }
  };

  const logout = () => {
    setUser(null);
    setToken(null);
    localStorage.removeItem('token');
    delete axios.defaults.headers.common['Authorization'];
    
    toast({
      title: 'Logged Out',
      description: 'You have been successfully logged out.',
    });
  };

  const updateProfile = async (updateData) => {
    try {
      const response = await axios.put(`${API}/auth/me`, updateData);
      setUser(response.data);
      
      toast({
        title: 'Profile Updated',
        description: 'Your profile has been updated successfully.',
      });

      return { success: true };
    } catch (error) {
      const message = error.response?.data?.detail || 'Update failed';
      toast({
        title: 'Update Failed',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    }
  };

  const forgotPassword = async (email) => {
    try {
      await axios.post(`${API}/auth/forgot-password`, { email });
      
      toast({
        title: 'Password Reset Email Sent',
        description: 'Check your email for password reset instructions.',
      });

      return { success: true };
    } catch (error) {
      const message = error.response?.data?.detail || 'Failed to send reset email';
      toast({
        title: 'Reset Failed',
        description: message,
        variant: 'destructive'
      });
      return { success: false, error: message };
    }
  };

  const isAdmin = () => {
    // Mock data'dan admin kullanıcısını kontrol et
    if (!user) return false;
    
    // Test için admin@bilal-parts.com email'ini admin olarak kabul et
    if (user.email === 'admin@bilal-parts.com') return true;
    
    return user.role === 'admin';
  };

  const value = {
    user,
    loading,
    isAuthenticated: !!user,
    isAdmin,
    login,
    register,
    logout,
    updateProfile,
    forgotPassword,
  };

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  );
};