import React from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from './ui/dialog';
import { Button } from './ui/button';
import { Badge } from './ui/badge';
import { Card, CardContent } from './ui/card';
import { Heart, ShoppingCart, Star, X, Package, Info, DollarSign } from 'lucide-react';
import { useCart } from '../contexts/CartContext';
import { useWishlist } from '../contexts/WishlistContext';
import { useAuth } from '../contexts/AuthContext';
import LoginModal from './auth/LoginModal';
import { mockData } from '../data/mockData';

const ProductDetailModal = ({ product, isOpen, onClose }) => {
  const { addToCart, loading: cartLoading } = useCart();
  const { toggleWishlist, checkInWishlist, loading: wishlistLoading } = useWishlist();
  const { isAuthenticated } = useAuth();
  const [isInWishlist, setIsInWishlist] = React.useState(false);



  React.useEffect(() => {
    const checkWishlistStatus = async () => {
      if (isAuthenticated && product && isOpen) {
        const inWishlist = await checkInWishlist(product.id);
        setIsInWishlist(inWishlist);
      }
    };
    
    if (isOpen) {
      checkWishlistStatus();
    }
  }, [product?.id, isAuthenticated, isOpen, checkInWishlist]);

  const handleAddToCart = async () => {
    if (!isAuthenticated) return;
    await addToCart(product.id, 1);
  };

  const handleToggleWishlist = async () => {
    if (!isAuthenticated) return;
    const result = await toggleWishlist(product.id);
    if (result.success) {
      setIsInWishlist(!isInWishlist);
    }
  };

  const formatPrice = (price) => {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD'
    }).format(price);
  };

  // Get similar products (same category, different subcategory)
  const getSimilarProducts = () => {
    if (!product) return [];
    return mockData.products
      .filter(p => p.category === product.category && p.id !== product.id)
      .slice(0, 4);
  };

  if (!product) return null;

  const similarProducts = getSimilarProducts();

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-7xl max-h-[95vh] overflow-hidden">
        <DialogHeader className="pb-4">
          <DialogTitle className="text-2xl font-bold text-gray-900">
            {product.name}
          </DialogTitle>
        </DialogHeader>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          {/* Left Column - Product Image and Basic Info */}
          <div className="space-y-4">
            {/* Product Image */}
            <div className="relative">
              <img
                src={product.image}
                alt={product.name}
                className="w-full h-80 object-cover rounded-lg"
              />
              
              {/* Stock Status Badge */}
              <div className="absolute top-4 left-4">
                {product.in_stock ? (
                  <Badge className="bg-green-500 hover:bg-green-600">
                    In Stock ({product.stock_quantity})
                  </Badge>
                ) : (
                  <Badge variant="destructive">
                    Out of Stock
                  </Badge>
                )}
              </div>

              {/* Wishlist Button */}
              <div className="absolute top-4 right-4">
                {isAuthenticated ? (
                  <Button
                    size="sm"
                    variant="ghost"
                    className={`h-10 w-10 p-0 rounded-full bg-white shadow-md hover:shadow-lg ${
                      isInWishlist ? 'text-red-600' : 'text-gray-600'
                    }`}
                    onClick={handleToggleWishlist}
                    disabled={wishlistLoading}
                  >
                    <Heart 
                      className={`h-5 w-5 ${isInWishlist ? 'fill-current' : ''}`} 
                    />
                  </Button>
                ) : (
                  <LoginModal>
                    <Button
                      size="sm"
                      variant="ghost"
                      className="h-10 w-10 p-0 rounded-full bg-white shadow-md hover:shadow-lg text-gray-600"
                    >
                      <Heart className="h-5 w-5" />
                    </Button>
                  </LoginModal>
                )}
              </div>
            </div>

            {/* Product Actions */}
            <div className="space-y-3">
              {isAuthenticated ? (
                <Button
                  onClick={handleAddToCart}
                  disabled={!product.in_stock || cartLoading}
                  className="w-full bg-red-600 hover:bg-red-700 disabled:bg-gray-300 h-12 text-lg"
                >
                  {cartLoading ? (
                    <div className="flex items-center space-x-2">
                      <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                      <span>Adding to Cart...</span>
                    </div>
                  ) : (
                    <div className="flex items-center space-x-2">
                      <ShoppingCart className="h-5 w-5" />
                      <span>
                        {product.in_stock ? 'Add to Cart' : 'Out of Stock'}
                      </span>
                    </div>
                  )}
                </Button>
              ) : (
                <LoginModal>
                  <Button className="w-full bg-red-600 hover:bg-red-700 h-12 text-lg">
                    <ShoppingCart className="h-5 w-5 mr-2" />
                    Add to Cart
                  </Button>
                </LoginModal>
              )}
            </div>
          </div>

          {/* Right Column - Product Details */}
          <div className="space-y-4">
                         {/* Basic Info */}
             <div className="space-y-3">
               <div className="flex items-center space-x-2">
                 <Package className="h-5 w-5 text-gray-500" />
                 <span className="text-sm text-gray-600">
                   SKU: {product.sku}
                 </span>
               </div>

               {product.catalogNumber && (
                 <div className="flex items-center space-x-2">
                   <Package className="h-5 w-5 text-gray-500" />
                   <span className="text-sm text-gray-600">
                     Catalog Number: {product.catalogNumber}
                   </span>
                 </div>
               )}

               <div className="flex items-center space-x-2">
                 <Info className="h-5 w-5 text-gray-500" />
                 <span className="text-sm text-gray-600">
                   Category: {product.category} {product.subcategory && `• ${product.subcategory}`}
                 </span>
               </div>

              {/* Rating */}
              <div className="flex items-center space-x-2">
                <div className="flex">
                  {[1, 2, 3, 4, 5].map((star) => (
                    <Star
                      key={star}
                      className={`h-4 w-4 ${
                        star <= 4 ? 'text-yellow-400 fill-current' : 'text-gray-300'
                      }`}
                    />
                  ))}
                </div>
                <span className="text-sm text-gray-600">(4.0) - 128 reviews</span>
              </div>
            </div>

            {/* Price */}
            <div className="space-y-2">
              <div className="flex items-center space-x-2">
                <DollarSign className="h-5 w-5 text-red-600" />
                <span className="text-2xl font-bold text-red-600">
                  {formatPrice(product.price)}
                </span>
                {product.originalPrice && product.originalPrice > product.price && (
                  <span className="text-lg text-gray-500 line-through">
                    {formatPrice(product.originalPrice)}
                  </span>
                )}
              </div>
              {product.originalPrice && product.originalPrice > product.price && (
                <Badge className="bg-green-100 text-green-800 border-green-200 text-xs">
                  Save {formatPrice(product.originalPrice - product.price)}
                </Badge>
              )}
            </div>

            {/* Description */}
            <div className="space-y-2">
              <h3 className="text-base font-semibold text-gray-900">Description</h3>
              <p className="text-gray-700 leading-relaxed text-sm">
                {product.description}
              </p>
            </div>

            {/* Specifications */}
            {product.specifications && (
              <div className="space-y-2">
                <h3 className="text-base font-semibold text-gray-900">Specifications</h3>
                <div className="grid grid-cols-1 sm:grid-cols-2 gap-3">
                  {Object.entries(product.specifications).map(([key, value]) => (
                    <div key={key} className="flex justify-between py-2 border-b border-gray-100">
                      <span className="text-sm font-medium text-gray-600">{key}</span>
                      <span className="text-sm text-gray-900">{value}</span>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>

        {/* Similar Products */}
        {similarProducts.length > 0 && (
          <div className="mt-8 space-y-4">
            <h3 className="text-xl font-bold text-gray-900">Similar Products</h3>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-3">
              {similarProducts.map((similarProduct) => (
                <Card key={similarProduct.id} className="hover:shadow-lg transition-shadow">
                  <CardContent className="p-3">
                    <img
                      src={similarProduct.image}
                      alt={similarProduct.name}
                      className="w-full h-28 object-cover rounded-lg mb-2"
                    />
                    <h4 className="font-semibold text-gray-900 text-sm line-clamp-2 mb-1">
                      {similarProduct.name}
                    </h4>
                    <p className="text-xs text-gray-500 mb-2">
                      {similarProduct.category} • {similarProduct.subcategory}
                    </p>
                    <div className="flex items-center justify-between">
                      <span className="font-bold text-red-600 text-sm">
                        {formatPrice(similarProduct.price)}
                      </span>
                                             <Button
                         size="sm"
                         variant="outline"
                         onClick={() => {
                           // Close current modal and trigger event to open new one
                           onClose();
                           // Use setTimeout to ensure current modal closes before opening new one
                           setTimeout(() => {
                             // Trigger a custom event to open the similar product modal
                             const event = new CustomEvent('openProductModal', {
                               detail: { product: similarProduct }
                             });
                             window.dispatchEvent(event);
                           }, 100);
                         }}
                       >
                         View
                       </Button>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          </div>
                )}
      </DialogContent>

    </Dialog>
  );
};

export default ProductDetailModal;
